<template>
  <div id="page-wrap" v-if="product">
    <div id="img-wrap">
      <img v-bind:src="product.imageUrl" />
    </div>
    <div id="product-details">
      <h1>{{ product.name }}</h1>
      <h3 id="price">${{ product.price }}</h3>
      <p>Average rating: {{ product.averageRating }}</p>
      <button
        id="add-to-cart"
        v-if="!itemIsInCart && !showSuccessMessage"
        v-on:click="addToCart"
      >
        Add to Cart
      </button>
      <button
        id="add-to-cart"
        class="green-button"
        v-if="!itemIsInCart && showSuccessMessage"
      >
        Successfully added item to cart!
      </button>
      <button id="add-to-cart" class="disable-button" v-if="itemIsInCart">
        This item is already in cart!
      </button>
      <h4>Description</h4>
      <p>{{ product.description }}</p>
    </div>
  </div>
  <NotFoundPage v-else />
</template>

<script>
import axios from "axios";
import NotFoundPage from "./NotFoundPage";
// import { XMLHttpRequestCustom } from "../utilities/httpRequest";

export default {
  name: "ProductDetailPage",
  components: { NotFoundPage },
  data() {
    return {
      product: {},
      cartItems: [],
      showSuccessMessage: false,
    };
  },
  computed: {
    itemIsInCart() {
      return this.cartItems.some((item) => item.idNum === this.product.idNum);
    },
  },
  methods: {
    async addToCart() {
      await axios.post("/api/users/12345/cart", {
        productId: this.$route.params.id,
      });
      this.showSuccessMessage = true;
      setTimeout(() => {
        this.$router.push("/products"); // immedidately navigate the suer back to the products page
      }, 1500);
    },
  },
  // a life cycle hook
  async created() {
    const { data: product } = await axios.get(
      `/api/products/${this.$route.params.id}`
    );
    this.product = product;
    const fetchCartItemsURL = "/api/users/12345/cart";
    const { data: cartItems } = await axios.get(fetchCartItemsURL);
    this.cartItems = cartItems;

    // const shoppingItems = await XMLHttpRequestCustom(fetchCartItemsURL);
    // console.log(shoppingItems);
    // // this.cartItems = await this.createRequest(
    // //   "/api/users/12345/cart",
    // //   this.resHandleSuccess,
    // //   this.resHandleError
    // // );
  },
};
</script>

<style scoped>
#page-wrap {
  margin-top: 16px;
  padding: 16px;
  max-width: 600px;
}

#img-wrap {
  text-align: center;
}

img {
  width: 400px;
}

#product-details {
  padding: 16px;
  position: relative;
}

#add-to-cart {
  width: 100%;
}

#price {
  position: absolute;
  top: 24px;
  right: 16px;
}

.green-button {
  background-color: green;
}

.disable-button {
  opacity: 0.6;
  background-color: grey;
  cursor: not-allowed;
}
</style>
